@use "../abstracts/" as *;

.icon {
  color: var(--clr-font);

  &--logo {
    width: torem(110);
    height: torem(32);
  }
  &--help-in-circle {
    width: torem(24);
    height: torem(24);
  }
  &--hamburger {
    width: torem(32);
    height: torem(32);
  }
  &--close-cross {
    width: torem(24);
    height: torem(24);
  }
  &--arrow-down {
    width: torem(24);
    height: torem(16);
  }
  &--check {
    width: torem(24);
    height: torem(24);
  }
  &--input-check {
    width: torem(15);
    height: torem(13);
  }
  &--check-circle {
    width: torem(24);
    height: torem(24);
    fill: none;
  }
  &--alert-triangle {
    width: torem(24);
    height: torem(24);
  }
  &--sun {
    width: torem(24);
    height: torem(24);
  }
  &--moon {
    width: torem(24);
    height: torem(24);
  }
}
