@use "../../abstracts/" as *;

.c-card {
  // card
  --clr-card-back: var(--clr-sym-card-back);
  --clr-card-shadow: var(--clr-sym-card-shadow);

  width: 100%;
  height: 100%;
  text-decoration: none;
  display: flex;
  flex-direction: column;

  flex: 1 0 300px;
  min-height: 367px;
  border-radius: 12px;
  background-color: var(--clr-card-back);
  box-shadow: 0 2px 4px var(--clr-card-shadow);
  padding: torem(30) torem(25);

  &__body {
    // display: flex;
    flex-grow: 1;
    pointer-events: none;
  }

  &--calendar {
    .c-card__body {
      display: flex;
      justify-content: center;
    }
  }

  &--tooltip,
  &--avatar {
    .c-card__body {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__type {
    font-weight: 400;
    font-size: torem(16);
    line-height: torem(19);
  }

  &__title {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;

    font-family: "Roboto", system-ui, sans-serif;
    font-weight: 500;
    font-size: torem(21);
    line-height: lh(25, 16);
    color: var(--clr-font);

    p {
      margin: 0;
    }
  }
}