@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap");
body {
  font-family: "Inter", system-ui, sans-serif;
  color: var(--clr-font);
}

h1 {
  font-weight: 500;
  font-size: torem(50);
  line-height: lh(64, 50);
}

h2 {
  font-weight: 500;
}

.inter-400 {
  font-family: "Inter", system-ui, sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

body {
  height: 100vh;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --layout-padding: 30px;
  --layout-width: 1110px;
  --percent-darken-by: 10;
  --clr-primary-1: #4263EB;
  --clr-primary-1-dark10: hsl(from var(--clr-primary-1) h s calc(l - var(--percent-darken-by)));
  --clr-primary-1-dark20: hsl(from var(--clr-primary-1) h s calc(l - var(--percent-darken-by)*2));
  --clr-primary-2: #7048E8;
  --clr-primary-2-dark10: hsl(from var(--clr-primary-2) h s calc(l - var(--percent-darken-by)));
  --clr-primary-2-dark20: hsl(from var(--clr-primary-2) h s calc(l - var(--percent-darken-by)*2));
  --clr-primary-3: #f784ad;
  --clr-gray-100: #f8f9fa;
  --clr-gray-200: rgb(221.125, 222.375, 223.875);
  --clr-gray-300: rgb(194.25, 195.75, 197.75);
  --clr-gray-400: rgb(167.375, 169.125, 171.625);
  --clr-gray-500: rgb(140.5, 142.5, 145.5);
  --clr-gray-600: rgb(113.625, 115.875, 119.375);
  --clr-gray-700: rgb(86.75, 89.25, 93.25);
  --clr-gray-800: rgb(59.875, 62.625, 67.125);
  --clr-gray-900: #212429;
  --clr-white: #fff;
  --clr-black: #212429;
  --clr-gray-1: #F8F9FA;
  --clr-gray-2: #DDE2E5;
  --clr-gray-3: #ACB5BD;
  --clr-gray-4: #495057;
  --clr-font: var(--clr-black);
  --clr-main-back: #F9FAFC;
  --clr-main-back-light: var(--clr-white);
  --clr-main-back-gray: var(--clr-gray-4);
  --clr-sym-card-back: var(--clr-main-back-light);
  --clr-sym-card-shadow: rgba(33, 36, 41, .065);
  --clr-sym-input-back: var(--clr-white);
  --clr-sym-calendar-title: #334D6E;
  --clr-sym-calendar-day: #90A0B7;
  --clr-sym-calendar-day-item: #334d6e;
  --clr-sym-calendar-between: var(--clr-font);
  --clr-sym-btn-text-dark: var(--clr-gray-4);
  --clr-sym-footer-back: var(--clr-white);
  --clr-sym-modal-window-back: var(--clr-white);
  --clr-sym-modal-window-border: var(--clr-gray-3);
  --clr-sym-skeleton-string: var(--clr-gray-2);
  --clr-sym-skeleton-runner-back: 255, 255, 255;
  --clr-sym-summary-arrow: var(--clr-gray-4);
  --clr-sym-checkbox-animation: 0, 0, 0;
  --clr-sym-input-back-disabled: var(--clr-gray-1);
}

