html[data-mode=dark] {
  --clr-font: var(--clr-white);
  --clr-main-back: var(--clr-gray-4);
  --clr-main-back-light: var(--clr-black);
  --clr-sym-card-shadow: rgba(255, 255, 255, 0.265);
  --clr-sym-card-back: var(--clr-black);
  --clr-sym-input-back: var(--clr-black);
  --clr-sym-calendar-title: var(--clr-font);
  --clr-sym-calendar-day: var(--clr-font);
  --clr-sym-calendar-day-item: var(--clr-gray-3);
  --clr-sym-btn-text-dark: var(--clr-font);
  --clr-sym-footer-back: var(--clr-black);
  --clr-sym-modal-window-back: var(--clr-black);
  --clr-sym-modal-window-border: var(--clr-gray-3);
  --clr-sym-skeleton-string: var(--clr-gray-4);
  --clr-sym-skeleton-runner-back: 128, 128, 128;
  --clr-sym-summary-arrow: var(--clr-white);
  --clr-sym-checkbox-animation: 255, 255, 255;
  --clr-sym-input-back-disabled: var(--clr-gray-4);
}

.l-buttons__heading {
  min-width: 16ch;
}
@media (max-width: 980px) {
  .l-buttons__heading {
    text-align: center;
    width: 100%;
  }
}

.l-container {
  max-width: calc(var(--layout-width) + var(--layout-padding) * 2);
  padding: 0 var(--layout-padding);
  margin: 0 auto;
}

.icon {
  color: var(--clr-font);
}
.icon--logo {
  width: 6.875rem;
  height: 2rem;
}
.icon--help-in-circle {
  width: 1.5rem;
  height: 1.5rem;
}
.icon--hamburger {
  width: 2rem;
  height: 2rem;
}
.icon--close-cross {
  width: 1.5rem;
  height: 1.5rem;
}
.icon--arrow-down {
  width: 1.5rem;
  height: 1rem;
}
.icon--check {
  width: 1.5rem;
  height: 1.5rem;
}
.icon--input-check {
  width: 0.9375rem;
  height: 0.8125rem;
}
.icon--check-circle {
  width: 1.5rem;
  height: 1.5rem;
  fill: none;
}
.icon--alert-triangle {
  width: 1.5rem;
  height: 1.5rem;
}
.icon--sun {
  width: 1.5rem;
  height: 1.5rem;
}
.icon--moon {
  width: 1.5rem;
  height: 1.5rem;
}

.l-components-nav__item {
  list-style: none;
}
.l-components-nav__item--mobile {
  margin-bottom: 1rem;
}
.l-components-nav__item:nth-last-child(2) {
  margin-bottom: 1.875rem;
}
.l-components-nav__item--mobile:nth-last-child(2) {
  margin-bottom: 4.5rem;
}
.l-components-nav__title--mobile {
  font-size: 1.25rem;
  line-height: 1.3;
  font-weight: 400;
  color: var(--clr-gray-3);
}
.l-components-nav ul {
  margin: 0;
  padding: 0;
}
.l-components-nav__link {
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 400;
  white-space: nowrap;
  color: var(--clr-font);
  text-decoration: none;
}
.l-components-nav__link--active {
  color: #D84910;
}

.l-footer {
  --clr-footer-back: var(--clr-sym-footer-back);
  background-color: var(--clr-footer-back);
  padding: 4.375rem 0;
  text-align: center;
}
.l-footer__transparent-back {
  background-color: transparent;
}

.l-header {
  --clr-header-back: var(--clr-main-back-light);
  background-color: var(--clr-header-back);
  padding: 2rem 0 0 0;
}
@media screen and (max-width: 600px) {
  .l-header {
    min-height: initial;
    padding: 1rem 0 0 0;
    margin-bottom: 0;
  }
}
.l-header__top {
  margin-bottom: 2.375rem;
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 600px) {
  .l-header__top .l-header__slot-mode-switch {
    display: none;
  }
}
@media screen and (max-width: 900px) {
  .l-header__top {
    margin-bottom: 1rem;
  }
}
@media screen and (max-width: 1200px) {
  .l-header__top {
    display: flex;
    justify-content: space-between;
  }
}
@media screen and (max-width: 600px) {
  .l-header__logo {
    margin: 0 auto;
  }
}
.l-header__heading {
  margin-top: 0;
}
@media screen and (max-width: 600px) {
  .l-header__heading {
    font-weight: 400;
    font-size: 1.75rem;
    line-height: 1.3571428571;
  }
}
@media screen and (max-width: 900px) {
  .l-header__heading {
    margin-bottom: 2rem;
  }
}
@media screen and (max-width: 900px) {
  .l-header .l-header ~ .c-tabs {
    margin-top: -1.875rem;
  }
}
.l-header--transparent-back {
  background-color: transparent;
}

.l-main-nav-mobile {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100%);
  background-color: var(--clr-main-back-light);
  transition: transform 250ms ease-in-out;
}
.l-main-nav-mobile[data-open] {
  transform: translateX(0);
}
.l-main-nav-mobile__top {
  display: flex;
  align-items: center;
  padding-top: 2rem;
  padding-bottom: 1rem;
}
.l-main-nav-mobile__logo {
  margin: 0 auto;
}

.l-page {
  background-color: var(--clr-main-back);
  display: grid;
  grid-template-rows: auto auto 1fr auto;
  min-height: 100vh;
  grid-template-columns: 100%;
}
.l-page--single {
  grid-template-rows: auto 1fr auto;
}
.l-page__horizontal-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.l-page__vertical-block {
  display: grid;
}
.l-page__single {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: 100%;
  gap: 3.5rem;
}
@media screen and (max-width: 1200px) {
  .l-page__single {
    display: block;
